import myaxios from '../utils/httpRequest'

const apiList = {
   find:`/findGeoResultList`,
   tree:`/getGeoResultTree`
}

export default {
    //goe
    findGeoResultList(name,status){
        return myaxios({
            method:'GET',
            url:apiList.find,
            params:{
                status:status,
                resultName:name,
                includeAnnex:false,
            }
        })
    },
    //geo
    getGeoResultTree(){
        return myaxios({
            method:'GET',
            url:apiList.tree,
        })
    }
}